import Vue from 'vue'
import Router from 'vue-router'
import Overview from './views/Overview.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/overview' // Default route
    },
    {
      path: '/overview',
      name: 'overview',
      component: Overview
    }
  ]
})
